@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
body{margin:0;padding:0;font-family:'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#111}

.App{display:flex;justify-content:space-around}.App--Configuration{flex:1 0 auto;width:25rem;max-width:25rem}.App--WorkSheet{display:flex;justify-content:center;padding:10mm;border:3px dashed #ddd}@page{size:A4;margin:10mm}@media print{.App a:visited{color:inherit}.App--Configuration{display:none}.App--WorkSheet{width:100%;padding:0;border:none}}

form h1{font-size:1.5rem}form h2{font-size:1rem;font-weight:normal;text-transform:uppercase}form label,form .Label{margin:0 0 0.5rem 0;display:flex;flex-direction:column}form label span,form .Label span{padding-bottom:0.125rem}form .Label--Checkbox{display:block}form select,form input{padding:0.25rem 0.125rem}.Collection{width:100%}.Collection--Print{margin-top:1rem;margin-bottom:0.25rem;display:flex;justify-content:space-between}.Collection--Page{width:210mm;height:270mm;border:3px dashed #eee;padding:3rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.Collection--Name input{font-size:2rem;width:10rem}.Collection--Images{width:100%;height:100%;display:flex;flex-wrap:wrap;justify-content:space-between}.Collection--Image{flex-grow:1;flex-basis:100% / 7;display:flex;flex-direction:column;justify-content:center;align-items:center;width:7rem}.Collection--Image img{opacity:0.2;width:4rem;height:4rem;object-fit:contain}.Collection--Image span{padding-top:0.25rem;font-size:1.25rem;font-weight:bold;white-space:nowrap}.Collection--Attributions{font-size:0.5rem;font-style:italic;padding:0;margin:0.25rem 0 0 0;text-align:center}@media print{.Collection--Print{display:none}.Collection--Page{border:none}}

.Button{min-width:9rem;padding:0.25rem;display:flex;align-items:center;cursor:pointer;transition:all 0.2s;background-color:#fff;color:#222;border:1px solid #222;border-radius:4px;font-weight:bold}.Button:hover{background-color:MediumSeaGreen;color:#fff}.Button--Icon{margin-left:0.5rem;font-size:1.5rem}.Button--Text{padding-left:0.5rem;padding-right:0.5rem;font-size:1.125rem}.Button--ButtonLink{padding:0;margin:0;border:none;background:none;text-decoration:underline;color:dodgerblue;color:-webkit-link;cursor:pointer}form h1{font-size:1.5rem}form h2{font-size:1rem;font-weight:normal;text-transform:uppercase}form label,form .Label{margin:0 0 0.5rem 0;display:flex;flex-direction:column}form label span,form .Label span{padding-bottom:0.125rem}form .Label--Checkbox{display:block}form select,form input{padding:0.25rem 0.125rem}.Configuration{display:flex;flex-direction:column;margin:1rem}.Configuration--Buttons{display:flex;justify-content:space-between}

.WorkSheet--Difficulty{position:absolute}.WorkSheet--Title{display:flex;font-size:2rem;font-weight:bold;justify-content:center;margin-top:0;margin-bottom:0.1rem}.WorkSheet--Instructions{text-align:center;font-size:1rem;margin:1rem 0 0.5rem 0;padding:0}.WorkSheet--Questions{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:0.25rem}.WorkSheet--Questions>*{display:flex;justify-content:center}.WorkSheet--Response{display:flex;flex-wrap:wrap;justify-content:center;width:40rem}.WorkSheet--Attributions{font-size:0.5rem;font-style:italic;padding:0;margin:0.25rem 0 0 0;text-align:center}

.Difficulty{display:flex;color:gold}

.PuzzleWord{margin-top:1.5rem;display:flex;justify-content:center}.PuzzleWord--LetterResultPair{display:flex;flex-direction:column;align-items:center;margin:0 0.5rem}.PuzzleWord--MappingArrow{display:flex;justify-content:center;width:2rem;height:2rem;line-height:2rem;padding:0.125rem;color:#555;transform:rotate(90deg)}

.Digits{display:flex;justify-content:center;font-size:2rem;line-height:2rem;font-weight:bold;border:1px solid #ccc;margin-left:-1px;margin-top:-1px}.Digits--Digit{width:2rem;height:2rem;padding:0.125rem;border-collapse:collapse;display:flex;justify-content:center}.Digits--Digit:not(:first-child){border-left:1px dashed #ccc}.Digits--Hidden span{visibility:hidden}@media print{.Digits--HiddenOnPrint span{visibility:hidden}}

.Question{display:flex;margin:0.25rem}.Question--MappingArrow{display:flex;justify-content:center;width:2rem;height:2rem;line-height:2rem;padding:0.125rem;color:#555}

.ResponseImage{display:flex;flex-direction:column;align-items:center;padding:0.2rem 0.2rem 0.2rem 1rem}.ResponseImage--CheckImage{display:flex;align-items:center}.ResponseImage--CheckImage div{border:1px solid #ccc;width:2rem;height:2rem;line-height:2rem;margin-right:0.25rem}.ResponseImage--CheckImage img{width:4rem;height:4rem;object-fit:contain}.ResponseImage--Label{font-size:1.25rem}

